.menusModal_container {
    position: absolute;
    top: 6rem;
    left: 0;
    width: 100%;
    height: calc(100vh - 6rem);
    background-color: #f3f3f3;
    overflow: hidden;
    overflow-y: auto;
    border-top: 0.05rem solid #bdbdbd;
    transition: all 0.3s;
    .adm-collapse {
        color: $primary;
        font: $fs_m_16;
    }
    .adm-list-item,
    .adm-list-body {
        background-color: transparent;
        font: $fs_m_16;
    }
    .adm-list-item-content {
        border: none;
    }
    .adm-list-default .adm-list-body {
        border-top: none;
        border-bottom: none;
    }
    .adm-list-item-content-arrow {
        color: $primary;
        font: $fs_m_16;
    }
}

.menusModal_container__hidden {
    width: 0;
    height: 0;
    overflow: hidden;
    opacity: 0;
}

.menusModal_menus {
    height: calc(100% - 4rem);
    overflow: hidden;
    overflow-y: auto;
}

.menusModal_contacts {
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    padding: 0.5rem 0;
    svg {
        margin: 0 0.5rem;
    }
}

