/* <--------------------------- * SECTION1 * --------------------------- */

.section9_container {
    padding-bottom: 4vw;
    //background-image: url("../../../../Assets/images/section9_bg.webp");
    background-position: 50% 100%;
    background-repeat: no-repeat;
    background-size: auto;
}

.section9_center {
    max-width: 144rem;
    margin: 0 auto;
    padding: 0 4vw;
    color: $primary;
    text-align: center;
    > h1 {
        font: $fs_m_48;
        margin-bottom: 1.6rem;
        padding-top: 5%;
        border-top: 0.1rem solid #bfbfbf;
    }
    > p {
        font: $fs_m_14;
        line-height: 1.8;
        margin-bottom: 4rem;
    }
}

.section9_inputWrap {
    > div {
        display: flex;
        align-items: center;
        > div {
            flex: 1;
            margin: 0 1rem;
        }
    }
}

.section9_inputWrap__textarea {
    padding-top: 1.5rem;
}

