/* <--------------------------- * SECTION1 * --------------------------- */

@media screen and (min-width: 1200px) {
    .section9_center {
        > p {
            padding: 0 8vw;
        }
    }
}

@media screen and (max-width: 1200px) {
    .section9_center {
        > h1 {
            font-size: 3.2rem;
        }
    }
}

@media screen and (max-width: 768px) {
    .section9_container {
        padding: 5vw 0;
    }
    .section9_center {
        > h1 {
            font-size: 2.2rem;
        }
    }
    .section9_inputWrap {
        > div {
            flex-direction: column;
            > div {
                margin-bottom: 1.5rem;
            }
        }
    }
    .section9_inputWrap__textarea {
        padding-top: 0;
    }
}
