.messageIcon_container {
    position: relative;
    transition: 0.3s;
    margin-bottom: 1.6rem;
    cursor: pointer;
}

.messageIcon_bg {
    display: block;
    filter: drop-shadow(0 0 8px rgba(0, 0, 0, 0.06)) drop-shadow(0 0 4px rgba(0, 0, 0, 0.14));
    transition: 0.3s;
}

.messageIcon_icon {
    position: absolute;
    left: 22px;
    top: 22px;
}

