@import "~/Constant/breakPoint.scss";
@import "~/Constant/font.scss";
@import "~/Constant/color.scss";

/* <------------------------------------ **** CONSTANT IMPORT END **** ------------------------------------ */

/* <------------------------------------ **** SECTION1 MIXIN START **** ------------------------------------ */

/** The demo mixin is ..........*/

@mixin demo {
}

/* <------------------------------------ **** SECTION1 MIXIN END **** ------------------------------------ */

/* <--------------------------- * SECTION1 * --------------------------- */

.section5_container {
    padding: 0 1.5rem 3vw;
}

.section5_service {
    padding: 7% 14%;
    max-width: 144rem;
    margin: -12vw auto 4rem;
    background-color: #000;
    font: $fs_m_14;
    color: #fff;
    text-align: center;
    h1 {
        font: $fs_m_48;
    }
    p {
        margin: 2rem 0 4rem;
        line-height: 1.8;
    }
    button {
        border-color: rgba(#fff, 0.6);
        color: #fff;
    }
}

/* <--------------------------- * SECTION1 * --------------------------- */

@media screen and (max-width: 1200px) {
    .section5_service {
        h1 {
            font-size: 3.2rem;
        }
    }
}

@media screen and (max-width: 768px) {
    .section5_container {
        padding: 5vw 1.5rem;
    }
    .section5_service {
        margin: 0;
        h1 {
            font-size: 2.2rem;
        }
    }
}
