/* <--------------------------- * SECTION1 * --------------------------- */

.section6_container {
    padding: 2vw 0 6vw;
    max-width: 144rem;
    margin: 0 auto;
    .swiper {
        padding: 0 1.5rem;
    }
    .swiper-pagination-bullet-active {
        background-color: #000;
    }
}

.section6_swiperItem {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 2rem;
    img {
        height: 7.2rem;
        width: 7.2rem;
    }
    p {
        width: 100%;
        margin: 2rem 0 4rem;
        font: $fs_m_14;
        line-height: 1.8;
        text-align: center;
        display: -webkit-box;
        line-clamp: 2;
        -webkit-line-clamp: 2;
        -webkit-box-orient: vertical;
        overflow: hidden;
        text-overflow: ellipsis;
    }
}

