/* <--------------------------- * SECTION1 * --------------------------- */

.footer_container {
    background-color: #e6e6e6;
    padding: 4vw 0;
}

.footer_center {
    max-width: 144rem;
    margin: 0 auto;
    display: flex;
    padding: 0 1.5rem;
    > div {
        flex: 1;
        margin-right: 9rem;
        &:last-child {
            margin-right: 0;
        }
    }
}

.footer_infoWrap {
    img {
        margin-bottom: 2rem;
    }
    p {
        font: $fs_m_14;
        color: #222;
        line-height: 1.8;
    }
    svg {
        margin: 1.5rem 1.5rem 0 0;
    }
}

.footer_links,
.footer_products,
.footer_contact {
    color: #222;
    h3 {
        font: $fs_m_16;
        margin-bottom: 1rem;
        font-weight: bold;
    }
    p {
        font: $fs_m_14;
        line-height: 2.6rem;
    }
}

.footer_changeLan {
    display: flex;
    align-items: center;
    font: $fs_m_14;
    color: #222;
    margin-top: 1rem;
    > span {
        margin-left: 2rem;
        position: relative;
        cursor: pointer;
        &::before {
            content: "";
            position: absolute;
            top: 50%;
            left: -1rem;
            transform: translateY(-50%);
            display: inline-block;
            height: 1.6rem;
            width: 0rem;
            border-left: 0.1rem solid #d8d8d880;
        }
    }
}

.footer_icons__mobile {
    padding: 0 1.5rem 2rem;
    svg {
        margin: 0 0.6rem 0.8rem;
    }
}

.footer_map__mobile {
    padding: 0 1.5rem 0;
    font: $fs_m_14;
}

.footer_copyright {
    border-top: 0.1rem solid #90909033;
    background-color: #e6e6e6;
    padding: 2vw 1.5rem;
    font: $fs_m_14;
    color: $primary;
    text-align: center;
}

/* <--------------------------- * SECTION1 * --------------------------- */

@media screen and (min-width: 768px) {
    .footer_map__mobile {
        display: none;
    }
}
