/* <--------------------------- * SECTION1 * --------------------------- */

.section7_container {
    background-color: rgba(230, 230, 230, 1);
}

.section7_center {
    //max-width: 144rem;
    //margin: 0 auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
    > div {
        padding: 0 1.5rem;
        width: 50%;
        min-width: 50%;
        max-width: 50%;
    }
}

.section7_left {
    height: 100%;
    h1 {
        font: $fs_m_48;
        font-weight: 500;
        color: #333;
        margin-bottom: 2rem;
    }
    p {
        font: $fs_m_14;
        color: $primary;
        margin-bottom: 2rem;
        line-height: 1.8;
    }
    b {
        color: #333;
    }
}

.section7_right {
    img {
        width: 100%;
        vertical-align: middle;
    }
}

/* <--------------------------- * SECTION1 * --------------------------- */

@media screen and (min-width: 1200px) {
    .section7_left {
        width: 47%;
        min-width: 47%;
        max-width: 47%;
    }
    .section7_right {
        width: 52%;
        min-width: 52%;
        max-width: 52%;
    }
}

@media screen and (min-width: 1600px) {
    .section7_left {
        h1,
        p,
        > div {
            padding: 0 6% 0 18%;
        }
    }
}

@media screen and (max-width: 1200px) {
    .section7_left {
        h1 {
            font-size: 3.2rem;
        }
    }
}

@media screen and (max-width: 768px) {
    .section7_container {
        padding: 5vw 0;
    }
    .section7_center {
        flex-direction: column-reverse;
        > div {
            width: 100%;
            max-width: 100%;
            min-width: 100%;
        }
    }
    .section7_right {
        margin-bottom: 2rem;
    }
    .section7_left {
        h1 {
            font-size: 2.2rem;
        }
    }
}
