@import "~/Constant/breakPoint.scss";
@import "~/Constant/font.scss";
@import "~/Constant/color.scss";

/* <--------------------------- * STYLE * --------------------------- */

.section1_servers {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    max-width: 144rem;
    margin: 0 auto;
    padding: 5vw 1.5rem;
    > div {
        width: 25%;
        padding: 0 1rem;
        display: flex;
        flex-direction: column;
    }
    img {
        width: 8.4rem;
        height: 8.4rem;
    }
    span {
        font: $fs_m_16;
        margin: 2rem 0 1rem;
    }
    p {
        font: $fs_m_14;
        line-height: 1.8;
        display: -webkit-box;
        -webkit-line-clamp: 2;
        line-clamp: 2;
        -webkit-box-orient: vertical;
        overflow: hidden;
        text-overflow: ellipsis;
    }
}

@media screen and (max-width: 768px) {
    .section1_servers {
        > div {
            width: 50%;
            margin-bottom: 2rem;
        }
    }
}

@media screen and (max-width: 640px) {
    .section1_servers {
        > div {
            width: 100%;
        }
    }
}
