/* <--------------------------- * SECTION1 * --------------------------- */

.section4_container {
    //background-image: url("../../../../Assets/images/section4_bg.webp");
    /*
    background-attachment: 控制背景图片在元素滚动时的行为
    scroll: 默认值。背景图随元素内容滚动而滚动
    fixed: 背景图会固定在元素的视口中
    local: 背景图随着元素内部内容的滚动而滚动，而不会离开元素的边界
    initial: 使用默认值
    inherit: 继承父元素
  */
    background-attachment: fixed;
    background-position: 50% 50%;
    background-repeat: no-repeat;
    background-size: auto;
    background-color: rgba(43, 48, 53, 1);
    color: rgba(255, 255, 255, 1);
    text-align: center;
    padding-top: 8vw;
    padding-bottom: 20vw;
    //position: relative;
}

.section4_content {
    color: #fff;
    max-width: 141rem;
    margin: 0 auto;
    padding: 0 1.5rem;
    font-family: $commonFamily;
    h1 {
        font-size: 3.2vw;
        margin-bottom: 1.6rem;
    }
    p {
        font-size: 2.4rem;
        margin-bottom: 1.5vw;
    }
}

.section4_btn {
    border-color: rgba(#fff, 0.6);
    color: #fff;
}

/* <--------------------------- * SECTION1 * --------------------------- */

@media screen and (min-width: 1200px) {
    .section4_content {
        p {
            padding: 0 8vw;
            opacity: 0.85;
        }
    }
}

@media screen and (max-width: 1200px) {
    .section4_content {
        h1 {
            font-size: 3.2rem;
        }
        p {
            font-size: 1.4rem;
        }
    }
}

@media screen and (max-width: 768px) {
    .section4_content {
        h1 {
            font-size: 2.2rem;
        }
    }
}
