.customizeInput_container {
    border: 0.1rem solid #dcdcdc;
    width: 100%;
    position: relative;
    padding: 2.4rem 0.8rem 0.8rem;
    outline: none;
    background-color: #f3f3f3;
}

.customizeInput_container__error {
    border-color: #d90000;
    .customizeInput_tips,
    .customizeInput_input {
        color: #d90000;
    }
}

.customizeInput_tips {
    position: absolute;
    //top: 50%;
    top: 1.5rem;
    left: 0.8rem;
    //transform: translateY(-50%);
    color: #777;
    font: $fs_m_14;
    line-height: 2rem;
    transition: 0.1s;
    opacity: 0.8;
    &::before {
        content: "*";
        color: red;
    }
}

.customizeInput_tips__top {
    top: 0.4rem;
    opacity: 0.6;
    font: $fs_m_12;
}

.customizeInput_errorMsg {
    position: absolute;
    top: 0;
    right: 2rem;
    color: #d90000;
    padding: 0 0.8rem;
    font: $fs_m_12;
    line-height: 1.5;
}

.customizeInput_errorMsg__hidden {
    //opacity: 0;
    display: none;
}

.customizeInput_input,
.customizeInput_textarea {
    width: 100%;
    height: 2rem;
    border: none;
    outline: none;
    background-color: transparent;
    color: #777;
    font: $fs_m_14;
    &::placeholder {
        color: transparent;
    }
}

.customizeInput_input__focus,
.customizeInput_textarea__focus {
    &::placeholder {
        opacity: 0.6;
        font: $fs_m_12;
        color: inherit;
    }
}

.customizeInput_textarea {
    height: 6.4rem;
    resize: none;
}

