.navTool_container {
    position: fixed;
    bottom: 2.2rem;
    right: 2.2rem;
    z-index: 50;
}

.navTool_chatOnline {
    transform: rotate(0) scale(1);
    transition: 0.2s;
}

.navTool_chatOnline__hidden {
    transform: rotate(360deg) scale(0);
}

.navTool_backTop {
    width: 6rem;
    height: 6rem;
    border-radius: 50%;
    background-color: #fff;
    display: flex;
    justify-content: center;
    align-items: center;
    box-shadow: rgba(122, 122, 122, 0.4) 0 0 15px;
    opacity: 0.8;
    cursor: pointer;
    svg {
        font-size: 2rem;
        color: #333;
    }
    &:active {
        box-shadow: rgba(122, 122, 122, 0.6) inset 0 2px 8px;
    }
}

.chatOnline_inputBox {
    padding: 2rem;
    > div {
        margin-bottom: 2rem;
    }
    p {
        font: $fs_m_14;
        color: #555;
        margin-bottom: 0.2rem;
    }
    i {
        color: red;
        font-style: normal;
    }
    input,
    textarea {
        background-color: #f0f2f7;
        border: none;
        outline: none;
        box-shadow: none;
        transition: 0.2s;
        color: #555;
        height: 3.4rem;
        padding: 0.6rem 1.2rem;
        font: $fs_m_14;
        width: 100%;
        border-radius: 0.4rem;
        &::placeholder {
            opacity: 0.6;
        }
        &:hover {
            background-color: #e5e7ec;
            box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.07);
        }
    }
    textarea {
        height: auto;
        resize: none;
    }
    button {
        height: 4rem;
        margin: 1rem 0 1.4rem;
        background-color: #333;
        border: 1px solid #e7eef5;
        outline: none;
        border-radius: 0.4rem;
        padding: 0.6rem 1.2rem;
        width: 100%;
        color: #fff;
        font: $fs_m_14;
        line-height: 1.4;
        transition: 0.2s;
        &:hover {
            opacity: 0.7;
        }
    }
}

.chatOnline_modal {
    position: absolute;
    bottom: 2.2rem;
    right: 2.2rem;
    top: unset;
    padding-bottom: 0;
    .ant-modal-content {
        padding: 0;
    }
    .ant-modal-header {
        background-color: #333;
        padding: 1.4rem 2rem;
    }
    .ant-modal-title {
        color: #fff;
        font: $fs_m_20;
        font-weight: bold;
    }
    .ant-modal-close-x {
        color: #fff;
        font: $fs_m_20;
    }
}

/* <--------------------------- * SECTION1 * --------------------------- */
