.languageModal_container {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.8);
}

.languageModal_top {
    color: #fff;
    line-height: 2rem;
    text-align: right;
    padding: 2rem;
    svg {
        font-size: 2rem;
    }
}

.languageModal_lan {
    width: 24rem;
    padding: 3rem;
}

.languageModal_country {
    //margin: 5rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 1.5rem 1rem;
    span {
        color: #fff;
        line-height: 3rem;
    }
}

.languageModal_country__img {
    width: 5rem;
    height: 3rem;
    overflow: hidden;
    position: relative;
    img {
        position: absolute;
        top: 0;
        left: 0;
    }
}

.languageModal_foot {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    padding: 1rem 0;
    color: #fff;
    text-align: center;
    font: $fs_m_14;
}

