.language_container {
    display: flex;
    align-items: center;
    cursor: pointer;
    svg {
        font-size: 2rem;
        cursor: pointer;
        margin-right: 1rem;
    }
    p {
        font: $fs_m_14;
        color: $primary;
        cursor: pointer;
    }
}

.switchLan_modal {
    top: 10%;
    padding-bottom: 0;
    .ant-modal-content {
        height: 80vh;
        border-radius: 1.5rem;
        box-shadow: 0 0 25px 0 rgba(0, 0, 0, 0.75);
        padding: 0;
        overflow: hidden;
    }
    .ant-modal-header {
        height: 10rem;
        border-bottom: 0.1rem solid #ddd;
        padding: 0 3rem;
        display: flex;
        align-items: center;
    }
    .ant-modal-title {
        font-size: 3.8rem;
        font-family: $commonFamily;
    }
    .ant-modal-close {
        width: 5rem;
        height: 5rem;
    }
    .ant-modal-close-x {
        font-size: 3rem;
        color: #000;
    }
    .ant-modal-body {
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        padding: 3rem;
    }
    .ant-modal-footer {
        width: 100%;
        height: 4rem;
        margin-top: 0;
        position: absolute;
        bottom: 0;
        display: flex;
        font: $fs_m_14;
        justify-content: center;
        align-items: center;
        box-shadow: 0 0 4px 0 rgba(0, 0, 0, 0.1);
        color: #000;
    }
}

.switchLan_country {
    height: 9rem;
    width: 15.6rem;
    padding: 1.5rem 1rem;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    font: $fs_m_14;
    color: $primary;
    cursor: pointer;
}

.switchLan_country__img {
    width: 5rem;
    height: 3rem;
    position: relative;
    overflow: hidden;
    img {
        position: absolute;
        top: 0;
        left: 0;
    }
}

/* <--------------------------- * SECTION1 * --------------------------- */
