/** The demo mixin is ..........*/

@mixin demo {
}

/* <------------------------------------ **** SECTION1 MIXIN END **** ------------------------------------ */

/* <--------------------------- * SECTION1 * --------------------------- */

.navToolMobile_container {
    position: fixed;
    bottom: -0.1rem;
    left: 0;
    right: 0;
    height: 6rem;
    z-index: 1;
    background-color: #fff;
    display: flex;
    align-items: center;
    padding: 1rem;
    > div {
        flex: 1;
        border-radius: 3rem;
        color: $primary;
        display: flex;
        justify-content: center;
        align-items: center;
        margin: 0 1rem;
        padding: 1.2rem 0.8rem;
        span {
            margin-left: 0.8rem;
            font: $fs_m_14;
        }
    }
}

.navToolMobile_sendNtn {
    background-color: #f3f3f3;
}

.navToolMobile_chatBtn {
    background-color: #000;
    svg,
    span {
        color: #fff;
    }
}

.navToolMobile_inquiryDrawer {
    border-radius: 1.6rem 1.6rem 0 0;
    .ant-drawer-header {
        border-bottom: none;
    }
    .customizeInput_container {
        background-color: transparent;
        border: none;
        border-bottom: 0.1rem solid rgba(#000, 0.1);
        margin: 1.2rem 0 2rem;
        padding: 1rem 0;
        &:focus {
            border-bottom-color: $primary;
        }
    }
    .customizeInput_tips {
        left: 0;
        top: 1rem;
    }
    .customizeInput_tips__top {
        top: -0.8rem;
    }
}

.navToolMobile_inquiryDrawer__top {
    display: flex;
    justify-content: space-between;
    align-items: center;
    font: $fs_m_16;
    font-weight: bold;
}

.navToolMobile_inquiryDrawer__btn {
    background-color: $primary;
    margin: 0.8rem 0;
    color: #fff;
    height: 3.6rem;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 0.6rem;
}

.navToolMobile_chatOnline__inputBox {
    padding: 2rem;
    > div {
        margin-bottom: 2rem;
    }
    p {
        font: $fs_m_14;
        color: #555;
        margin-bottom: 0.2rem;
    }
    i {
        color: red;
        font-style: normal;
    }
    input,
    textarea {
        background-color: #f0f2f7;
        border: none;
        outline: none;
        box-shadow: none;
        transition: 0.2s;
        color: #555;
        height: 3.4rem;
        padding: 0.6rem 1.2rem;
        font: $fs_m_14;
        width: 100%;
        border-radius: 0.4rem;
        &::placeholder {
            opacity: 0.6;
        }
        &:hover {
            background-color: #e5e7ec;
            box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.07);
        }
    }
    textarea {
        height: auto;
        resize: none;
    }
    button {
        height: 4rem;
        margin: 1rem 0 1.4rem;
        background-color: #333;
        border: 1px solid #e7eef5;
        outline: none;
        border-radius: 0.4rem;
        padding: 0.6rem 1.2rem;
        width: 100%;
        color: #fff;
        font: $fs_m_14;
        line-height: 1.4;
        transition: 0.2s;
        &:hover {
            opacity: 0.7;
        }
    }
}

.navToolMobile_chatOnline {
    position: absolute;
    bottom: 0;
    right: 0;
    left: 0;
    top: 0;
    padding-bottom: 0;
    max-width: 100vw;
    .ant-modal-content {
        padding: 0;
        height: 100%;
    }
    .ant-modal-header {
        background-color: #333;
        padding: 1.4rem 2rem;
    }
    .ant-modal-title {
        color: #fff;
        font: $fs_m_20;
        font-weight: bold;
    }
    .ant-modal-close-x {
        color: #fff;
        font: $fs_m_20;
    }
}

/* <--------------------------- * SECTION1 * --------------------------- */

@media screen and (max-width: 768px) {
    .navToolMobile_container {
        box-shadow: 0 0 6px rgba(0, 0, 0, 0.1);
    }
}
