@import "~/Constant/breakPoint.scss";
@import "~/Constant/font.scss";
@import "~/Constant/color.scss";

/* <--------------------------- * SECTION1 * --------------------------- */

.header_container {
    position: absolute;
    width: 100%;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    z-index: 50;
    max-width: 144rem;
    padding: 0 1.5rem;
}

.header_top {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 4rem;
    > p {
        color: $primary;
        font: $fs_m_14;
    }
}

.header_topRight {
    display: flex;
    align-items: center;
}

.header_contacts {
    display: flex;
    align-items: center;
    svg {
        font-size: 3rem;
        margin-right: 1rem;
        cursor: pointer;
    }
}

.header_language {
    display: flex;
    align-items: center;
    svg {
        font-size: 2rem;
        cursor: pointer;
        margin-right: 1rem;
    }
}

.header_bottom {
    height: 7.2rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.header_logo {
    height: 6rem;
    img {
        height: 100%;
    }
}

.header_menuList {
    font: $fs_m_14;
    color: $primary;
    display: flex;
    align-items: center;
}

.header_menuItem {
    padding: 0 2rem;
    font: $fs_m_16;
    cursor: pointer;
}

.header_menuItem__active {
    font-weight: bold;
}

.ant-dropdown-menu-title-content {
    color: #666;
    font-size: 1.6rem;
    font-family: $commonFamily;
}

/* <--------------------------- * SECTION1 * --------------------------- */
