@import "~/Constant/font.scss";
@import "~/Constant/color.scss";

/* <------------------------------------ **** CONSTANT IMPORT END **** ------------------------------------ */

/* <------------------------------------ **** SECTION1 MIXIN START **** ------------------------------------ */

/** The demo mixin is ..........*/

@mixin demo {
}

/* <------------------------------------ **** SECTION1 MIXIN END **** ------------------------------------ */

/* <--------------------------- * SECTION1 * --------------------------- */

.section8_container {
    padding: 4vw 0;
}

.section8_center {
    max-width: 144rem;
    margin: 0 auto 1.8rem;
    padding: 0 1.5rem;
    > h1 {
        font: $fs_m_48;
        margin-bottom: 1.6rem;
        text-align: center;
        color: $primary;
    }
    > p {
        font: $fs_m_14;
        margin-bottom: 4rem;
        line-height: 1.8;
        text-align: center;
    }
}

.section8_newsList {
    .swiper-pagination-bullet {
        width: 1.4rem;
        height: 1.4rem;
        background-color: #bbb;
    }
    .swiper-pagination-bullet-active {
        background-color: #fff;
        border: 0.1rem solid #000;
    }
}

.section8_newsItem {
    width: 33.3%;
    height: 100%;
    margin-right: 2rem;
    margin-bottom: 4rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    &:last-child {
        margin-right: 0;
    }
    img {
        width: 100%;
        margin-bottom: 1rem;
    }
    span {
        font: $fs_m_18;
        margin: 2rem 0 1rem;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        width: 100%;
        text-align: center;
    }
    p {
        font: $fs_m_14;
        line-height: 1.8;
        color: #777;
        text-align: center;
        overflow: hidden;
        text-overflow: ellipsis;
        display: -webkit-box;
        line-clamp: 2;
        -webkit-line-clamp: 2;
        -webkit-box-orient: vertical;
    }
}

/* <--------------------------- * SECTION1 * --------------------------- */

@media screen and (min-width: 1200px) {
    .section8_center {
        > p {
            padding: 0 8vw;
        }
    }
}

@media screen and (max-width: 1200px) {
    .section8_center {
        > h1 {
            font-size: 3.2rem;
        }
    }
}

@media screen and (max-width: 768px) {
    .section8_center {
        > h1 {
            font-size: 2.2rem;
        }
    }
    .section8_newsItem {
        span,
        p {
            padding: 0 1rem;
        }
    }
}
