@import "~/Constant/breakPoint.scss";
@import "~/Constant/font.scss";
@import "~/Constant/color.scss";

/* <--------------------------- * SECTION1 * --------------------------- */

.headerMobile_container {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 51;
    background-color: #f3f3f3;
}

.headerMobile_top {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 1rem;
    height: 6rem;
    position: relative;
}

.headerMobile_menusIcon svg {
    font-size: 2rem;
}

.headerMobile_logo {
    height: 100%;
    padding: 0.4rem 0;
    position: absolute;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    img {
        height: 100%;
    }
}

.headerMobile_right {
    display: flex;
    align-items: center;
    > div {
        display: flex;
        align-items: center;
    }
    svg {
        margin: 0 0.5rem;
    }
}

.headerMobile_language {
    > svg {
        font-size: 2rem;
        margin-left: 1.6rem;
    }
}

/* <--------------------------- * SECTION1 * --------------------------- */

@media screen and (max-width: 768px) {
    .headerMobile_contacts {
        display: none !important;
    }
}
