/** The demo mixin is ..........*/

@mixin demo {
}

/* <------------------------------------ **** SECTION1 MIXIN END **** ------------------------------------ */

/* <--------------------------- * SECTION1 * --------------------------- */

.section3_container {
    color: #000;
    padding-bottom: 3vw;
}

.section3_center {
    max-width: 141rem;
    margin: 0 auto;
    padding: 0 1.5rem;
    text-align: center;
    h1 {
        font: $fs_m_48;
        color: #000;
        font-weight: 400;
        margin-bottom: 1.6rem;
    }
    p {
        font: $fs_m_14;
        line-height: 1.8;
    }
}

.section3_productList {
    display: flex;
    flex-wrap: wrap;
    margin-top: 4rem;
    > div {
        width: 25%;
        padding: 0 1rem;
        display: flex;
        flex-direction: column;
        justify-content: center;
        margin-bottom: 2rem;
    }
    img {
        width: 100%;
    }
    span {
        font: $fs_m_16;
        color: #000;
        margin: 2rem 0 1rem;
    }
}

/* <--------------------------- * SECTION1 * --------------------------- */

@media screen and (min-width: 1200px) {
    .section3_container {
        p {
            padding: 0 8vw;
        }
    }
}

@media screen and (max-width: 1200px) {
    .section3_container {
        h1 {
            font-size: 3.2rem;
        }
    }
}

@media screen and (max-width: 768px) {
    .section3_container {
        h1 {
            font-size: 2.2rem;
        }
    }
    .section3_container {
        padding: 5vw 0;
    }
    .section3_productList {
        > div {
            width: 50%;
        }
    }
}
