@import "~/Constant/breakPoint.scss";
@import "~/Constant/font.scss";
@import "~/Constant/color.scss";

/* <--------------------------- * STYLE * --------------------------- */

.section1_container {
    width: 100%;
    position: relative;
}

.section1_bg {
    width: 100%;
}

.section1_center {
    max-width: 144rem;
    padding: 0 1.5rem;
    position: absolute;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    width: 100%;
    height: 100%;
}

.section1_content {
    position: absolute;
    //width: 41%;
    top: 50%;
    transform: translateY(-50%);
    color: $primary;
    padding-right: 16vw;
    font-family: $commonFamily;
}

.section1_title {
    font-size: 2.5vw;
    font-family: $commonFamily;
    line-height: 1.3;
}

.section1_description {
    margin: 1rem 0 3rem;
    font-size: 1.5rem;
    line-height: 1.8;
}

.section1_description__768 {
    position: absolute;
    top: calc(20% - 6rem);
    left: 0;
    padding: 0 1.5rem;
    font-size: 2.2rem;
    text-align: center;
    color: $primary;
    font-family: $commonFamily;
}

.section1_knowMoreBtn {
    height: 5.06rem;
    border: 0.1rem solid rgba(34, 34, 34, 1);
    padding: 1.4rem 3.8rem;
    color: $primary;
    border-radius: 3rem;
}

@media screen and (max-width: 1200px) {
    .section1_title {
        font-size: 1.6rem;
    }
    .section1_description {
        font-size: 1.4rem;
    }
}

@media screen and (max-width: 1064px) {
    .section1_description {
        width: 60%;
    }
}

@media screen and (min-width: 1065px) {
    .section1_title,
    .section1_description {
        width: 50%;
    }
}
