@mixin demo {
}

/* <------------------------------------ **** SECTION1 MIXIN END **** ------------------------------------ */

/* <--------------------------- * SECTION1 * --------------------------- */

.section2_container {
    padding-bottom: 4vw;
}

.section2_center {
    max-width: 144rem;
    margin: 0 auto;
    padding: 0 1.5rem;
    text-align: center;
    color: #333;
    h1 {
        font: $fs_m_48;
        color: #000;
        font-weight: 400;
        padding-top: 5%;
        border-top: 0.1rem solid #bfbfbf;
    }
    h3 {
        font: $fs_m_16;
        font-weight: bold;
        margin: 1.6rem 0;
        line-height: 1.8;
    }
    b {
        color: #000;
    }
    p {
        font: $fs_m_14;
        margin-bottom: 4rem;
        line-height: 1.8;
    }
}

.section2_productionList {
    overflow: hidden;
    max-width: 141rem;
    margin: 0 auto;
    position: relative;
    //height: 48rem;
    user-select: none;
    .swiper-pagination {
        .swiper-pagination-bullet {
            width: 1.4rem;
            height: 1.4rem;
            border-radius: 50%;
            border: 0.1rem solid transparent;
            background-color: #bbb;
            opacity: 0.3;
            margin: 0 0.5rem;
            cursor: pointer;
        }
        .swiper-pagination-bullet-active {
            background-color: #fff;
            border: 0.1rem solid #000;
            opacity: 1;
        }
    }
}

.section2_productionItem {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-right: 2rem;
    margin-bottom: 5rem;
    font: $fs_m_20;
    color: #000;
    img {
        width: 100%;
    }
    span {
        margin-top: 2rem;
    }
}

/* <--------------------------- * SECTION1 * --------------------------- */

@media screen and (min-width: 1200px) {
    .section2_center {
        h3,
        p {
            padding: 0 8vw;
        }
    }
}

@media screen and (max-width: 1200px) {
    .section2_center {
        h1 {
            font-size: 3.2rem;
        }
        h3 {
            font-size: 1.4rem;
        }
    }
    .section2_productionItem {
        font-size: 1.6rem;
    }
}

@media screen and (max-width: 768px) {
    .section2_center {
        h1 {
            font-size: 2.2rem;
        }
    }
    .section2_container {
        padding: 5vw 0;
    }
}
